	module.exports = {"data":{"markdownRemark":{"html":"<h2>List Style Type Property</h2>\n<p>This property changes the appearance of the list item marker. The default value is <code>disc</code>. You can also give a value of <code>none</code> if you don’t want item markers shown. It is part of the shorthand <code>list-style</code> property. </p>\n<p>The following is a group of lists that indicate some possible keywords and their respective results. A more complete list can be found on <a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/list-style-type\">MDN</a>, but be aware that browser support may limited for some newer keywords.</p>\n<p><img src=\"https://github.com/kaithrendyle/guide-photos/blob/master/lists-1.png\" alt=\"list examples\"></p>\n<p><img src=\"https://github.com/kaithrendyle/guide-photos/blob/master/lists-2.png\" alt=\"list examples\"></p>\n<p>*Note: <code>upper-latin</code> and <code>upper-alpha</code> produce the same results, as do <code>lower-latin</code> and <code>lower-alpha</code>. The only difference is that <code>upper-latin</code> and <code>lower-latin</code> are not supported in IE6 or IE7.</p>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/CSS/list-style-type\">List-style-type property on MDN</a></p>","fields":{"slug":"/css/properties/list-style-type-property/"},"frontmatter":{"title":"List Style Type Property"}}},"pathContext":{"slug":"/css/properties/list-style-type-property/"}}

